import axios from "axios";

const API_URL = "http://localhost:8000/auth"; // NestJS API

const authService = {
  signup: async (data: { name: string; surname?: string; email: string }) => {
    const res = await axios.post(`${API_URL}/signup`, data);
    return res.data;
  },

  signin: async (email: string) => {
    const res = await axios.post(`${API_URL}/signin`, { email });
    if (res.data.access_token) {
      localStorage.setItem("access_token", res.data.access_token);
    }
    return res.data;
  },

  verifyToken: async () => {
    const token = localStorage.getItem("access_token");
    if (!token) return null;

    const res = await axios.get(`${API_URL}/verify-token?token=${token}`);
    return res.data.user;
  },
};

export default authService;
